"use client"
import { IconChevronDown } from '@tabler/icons-react';
import { Burger, Center, Container, Group, Menu } from '@mantine/core';
import { useDisclosure } from '@mantine/hooks';
import Logo from '../../../../public/Logo.png'
import classes from './Header.module.css';
import Image from 'next/image';
import Link from 'next/link';
const links: { link: string | { link: string; label: string }[]; label: string }[] = [
    { link: '/', label: 'Главная' },
    { link: '/dashboard', label: 'Дашборд' },
    { link: '/analytics', label: 'Аналитика' },
    { link: '/login', label: 'Войти' },
    { link: '/registration', label: 'Регистрация' },
    { link: '/profile', label: 'Профиль' },
    { link: '/leave', label: 'Выйти' },
];


export function Header() {

    let isAuth = false

    const [opened, { toggle }] = useDisclosure(false);

    const visibleLinks = links.filter(link => {
        if (isAuth) {
            return link.label !== 'Войти' && link.label !== 'Регистрация';
        } else {
            return link.label !== 'Выйти' && link.label !== 'Профиль';
        }
    });

    const items = visibleLinks.map((link) => {
        const menuItems = Array.isArray(link.link)
            ? link.link.map((item) => (
                <Menu.Item key={item.link}>{item.label}</Menu.Item>
            ))
            : null;

        if (menuItems) {
            return (
                <Menu key={link.label} trigger="hover" transitionProps={{ exitDuration: 0 }} withinPortal>
                    <Menu.Target>
                        <a
                            href="#"
                            className={classes.link}
                            onClick={(event) => event.preventDefault()}
                        >
                            <Center>
                                <span className={classes.linkLabel}>{link.label}</span>
                                <IconChevronDown size={14} stroke={1.5} />
                            </Center>
                        </a>
                    </Menu.Target>
                    <Menu.Dropdown>{menuItems}</Menu.Dropdown>
                </Menu>
            );
        }

        return (
            <Link
                key={link.label}
                href={typeof link.link === 'string' ? link.link : '#'}
                className={classes.link}
            >
                {link.label}
            </Link>
        );
    });

    return (
        <header className={classes.header}>
            <Container size="md">
                <div className={classes.inner}>
                    <Image
                        src={Logo}
                        width={100}
                        height={30}
                        alt="Logo"
                    />
                    <Group gap={5} visibleFrom="sm">
                        {items}
                    </Group>
                    <Burger opened={opened} onClick={toggle} size="sm" hiddenFrom="sm" />
                </div>
            </Container>
        </header>
    );
}